/* Supplemental styles for Series 2 */
.resort-calendar,
.event-permalink {
    .event-calendar-wrapper,
    .event-details {
        .left {
            float: left !important;
        }

        .right {
            float: right !important;
        }

        .clearfix:before, .clearfix:after {
            content: " ";
            display: table;
        }

        .clearfix:after {
            clear: both;
        }

        .hide {
            display: none;
        }

        .invisible {
            visibility: hidden;
        }

        .antialiased {
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
        }

        img {
            display: inline-block;
            vertical-align: middle;
        }

        textarea {
            height: auto;
            min-height: 50px;
        }

        select {
            width: 100%;
        }

        .row {
            margin: 0 auto;
            max-width: none;
            width: 100%;

            &:before,
            &:after {
                content: " ";
                display: table;
            }

            &:after {
                clear: both;
            }

            .row {
                margin: 0 -0.9375rem;
                max-width: none;
                width: auto;

                &:before,
                &:after {
                    content: " ";
                    display: table;
                }

                &:after {
                    clear: both;
                }
            }
        }


        .row.collapse > .column,
        .row.collapse > .columns {
            padding-left: 0;
            padding-right: 0;
        }

        .row.collapse .row {
            margin-left: 0;
            margin-right: 0;
        }

        .row .row.collapse {
            margin: 0;
            max-width: none;
            width: auto;
        }

        .row .row.collapse:before, .row .row.collapse:after {
            content: " ";
            display: table;
        }

        .row .row.collapse:after {
            clear: both;
        }

        .column,
        .columns {
            padding-left: 0.9375rem;
            padding-right: 0.9375rem;
            width: 100%;
            float: left;
        }

        .column + .column:last-child,
        .columns + .column:last-child, .column +
        .columns:last-child,
        .columns +
        .columns:last-child {
            float: right;
        }

        .column + .column.end,
        .columns + .column.end, .column +
        .columns.end,
        .columns +
        .columns.end {
            float: left;
        }

